import uniRate from '@/components/uni-rate/uni-rate.vue';
import foot from '../component/foot'
import {
	mapGetters
} from 'vuex'
export default {
	components: {
		uniRate,
		foot
	},
	props:{
		shopIndex: {
			type: Object,
			default: {}
		},
	},
	computed: {
		...mapGetters(['shopId']),
		// 店铺照片
		shopImg() {
			if(this.shopIndex.images) {
				return this.shopIndex.images.filter(v=>{
					return v.type == 1
				})
			}
		},
		// 经营执照|食品许可
		LicenseList() {
			if(this.shopIndex.images) {
				return this.shopIndex.images.filter(v=>{
					return v.type == 2 || v.type == 4
				})
			}
		},
		// 健康证
		cardList() {
			if(this.shopIndex.images) {
				return this.shopIndex.images.filter(v=>{
					return v.type == 3
				})
			}
		},
		commentList() {
			if(this.shopIndex.comments && this.shopIndex.comments.list) {
				return this.shopIndex.comments.list.slice(0,2)
			}
		}
		
		
	},
	data() {
		return {
			score: 0,
			total_score: 0,
		}
	},
	onLoad(option) {
		// let qrId = decodeURIComponent(option.scene)
	},
	onShow() {
		console.log("shopId", this.shopId)
	},
	methods: {
		isShowFullText(v) {
			return v.split("").length >= 56
		},
		previewImage(v, e) {
			let list = [];
			const current = e.currentTarget.dataset.src;
			v.map( el => {
				list.push(el.image_url)
			})
			uni.previewImage({
				urls: list,
				current: current,
			})
		},
		_changeLike(item, bl, index) {
			this.$emit("changeLike", {item, bl, index})
		},
		_changeFullText(e) {
			this.$emit("changeFullText", {e})
		},
		_switchPostComments() {
			uni.navigateTo({
				url: "/pages/index/postComments"
			})
		},
	}
}
